


























export default {
	props:
		units:
			type: String
			default: 'imperial'

		temperature:
			type: [String,Number]
			default: null

		pressure:
			type: String
			default: null

		humidity:
			type: String
			default: null

	computed:
		unitIndicator: ->
			return if @units == 'imperial' then 'wi-fahrenheit' else 'wi-celsius'
}



