























export default {
	props:
		speed:
			type: [Number,String]

		direction:
			type: [Number,String]

	data: ->
		cardinalLabels: ["N","NNE","NE","ENE","E","ESE", "SE", "SSE","S","SSW","SW","WSW","W","WNW","NW","NNW"]

	computed:
		cardinal: ->
			index = Math.round((@direction/22.5))
			return @cardinalLabels[index%16]

		styleObject: ->
			transform: "rotate(#{@direction}deg)"
}



