

















import EventBus from '../logic/events'

export default {
	data: -> location: ''

	computed:
		isDisabled: ->return @location.trim() == ''

	beforeMount: ->
		EventBus.$on 'location-created', @clearInputs

	methods:
		addLocation: -> EventBus.$emit 'add-location', @location

		clearInputs: ->
			@location = ''
			$(@$refs.input).blur()
}



