




















export default {
	props:
		units:
			type: String
			default: 'imperial'

		high:
			type: [String,Number]
			required: true

		low:
			type: [String,Number]
			required: true

	computed:
		unitIndicator: ->
			return if @units == 'imperial' then 'wi-fahrenheit' else 'wi-celsius'
}



